import React,{useEffect, useState} from 'react'
import  './covid.css';


const Covid = () => {

    const [data, setData] = useState([]);

    const getCovidData= async () =>{
        try{
            const res= await fetch('https://api.covid19india.org/data.json');
            const actualData = await res.json();
            console.log(actualData.statewise[0]);
            setData(actualData.statewise[0]);

        }
        catch(err) {
            console.log(err);
        }

    }

    useEffect(() => {
        getCovidData();
    }, []);

    return (
        <>
            <h1><span style={{color: 'red'} }>.</span> LIVE</h1>
            <h2>COVID-19 CORONAVIRUS TRACKER</h2>

            <ul>
            <li className="card">
                <div className="card__main">
                    <div className="card__inner">
                        <p className="card__name"><span> OUR </span> COUNTRY</p>
                        <p className="card__total card__small">INDIA</p>
                    </div>
                </div>
            </li>
            <li className="card">
                <div className="card__main_2">
                    <div className="card__inner_2">
                        <p className="card__name_2"><span> TOTAL </span> RECOVERED</p>
                        <p className="card__total_2 card__small">{data.recovered}</p>
                    </div>
                </div>
            </li>
            <li className="card">
                <div className="card__main_3">
                    <div className="card__inner_3">
                        <p className="card__name_3"><span> TOTAL </span> CONFIRMED</p>
                        <p className="card__total_3 card__small">{data.confirmed}</p>
                    </div>
                </div>
            </li>
            <li className="card">
                <div className="card__main_4">
                    <div className="card__inner_4">
                        <p className="card__name_4"><span> TOTAL </span> DEATH</p>
                        <p className="card__total_4 card__small">{data.deaths}</p>
                    </div>
                </div>
            </li>
            <li className="card">
                <div className="card__main_5">
                    <div className="card__inner_5">
                        <p className="card__name_5"><span> TOTAL </span> ACTIVE</p>
                        <p className="card__total_5 card__small">{data.active}</p>
                    </div>
                </div>
            </li>
            <li className="card">
                <div className="card__main_6">
                    <div className="card__inner_6">
                        <p className="card__name_6"><span> LAST </span> UPDATED</p>
                        <p className="card__total_6 card__small">{data.lastupdatedtime}</p>
                    </div>
                </div>
            </li>

            </ul>
        </>
    )
}

export default Covid
